<template>
    <el-dialog title='批量更新' :visible.sync="dialogForm" class="updatePwd">

        <el-form :model="form">
            <el-form-item label="域名" :label-width="formLabelWidth">
                <el-input
                        v-model.trim="form.url"
                        type="textarea"
                        :rows="2"
                        placeholder="请输入域名, 用','连接">
                </el-input>
                <div @click="formatUrlsStr()" class="geshi">格式化</div>
            </el-form-item>
            <el-form-item label="用途:" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.purpose" >
                    <el-option value="" label="不修改"></el-option>
                    <el-option
                            v-for="(item, $key) in purpose"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="微信状态:" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.wx" >
                    <el-option value="" label="不修改"></el-option>
                    <el-option
                            v-for="(item, $key) in wx"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="qq状态:" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.qq" >
                    <el-option value="" label="不修改"></el-option>
                    <el-option
                            v-for="(item, $key) in qq"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="域名状态:" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.status" >
                    <el-option value="" label="不修改"></el-option>
                    <el-option
                            v-for="(item, $key) in status"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="解析状态:" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.parsing_status" >
                    <el-option value="" label="不修改"></el-option>
                    <el-option
                            v-for="(item, $key) in parsing_status"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="测试分组:" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.group_num" >
                    <el-option value="" label="不修改"></el-option>
                    <el-option
                            v-for="(item, $key) in group_num"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">取 消</el-button>
            <el-button type="primary" @click="submit()">提交</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
    .geshi{
        border:1px solid #ddd;
        border-radius:4px;
        height:40px;
        line-height:40px;
        text-align:center;
        margin-top:20px;
        cursor:pointer;
    }
    .updatePwd{
        .el-dialog__header{
            padding:30px;
            .el-dialog__headerbtn{
                width:50px;
                height:50px;
                margin:-30px -30px 0 0;
            }
        }
        .el-dialog__body{
            padding:30px 50px 0 0;
        }
    }
</style>

<script>
    import QuRequest from '../../core/QuRequest'

    const quRequest = new QuRequest()

    export default {
        data() {
            return {
                group_num:[
                    { 'name': '未分组', 'value': '0'},
                    { 'name': '1组','value': '1'},
                    { 'name': '2组', 'value': '2'},
                    { 'name': '3组', 'value': '3'},
                    { 'name': '4组','value': '4'},
                    { 'name': '5组', 'value': '5'},
                    { 'name': '6组', 'value': '6'},
                    { 'name': '7组', 'value': '7'},
                    { 'name': '8组','value': '8'},
                    { 'name': '9组', 'value': '9'},
                    { 'name': '10组', 'value': '10'},
                ],
                status:[
                    { 'name': '启用', 'value': '0'},
                    { 'name': '禁用','value': '1'},
                    { 'name': '备用', 'value': '2'},
                ],
                wx:[
                    { 'name': '正常', 'value': '0'},
                    { 'name': '拦截','value': '1'},
                ],
                qq:[
                    { 'name': '正常', 'value': '0'},
                    { 'name': '拦截','value': '1'},
                ],
                purpose:[
                    { 'name': '默认', 'value': '0'},
                    { 'name': '微信','value': '1'},
                    { 'name': '朋友圈', 'value': '2'},
                    { 'name': '二维码', 'value': '3'},
                    { 'name': 'QQ','value': '4'},
                    { 'name': '专用', 'value': '5'},
                    { 'name': '微博', 'value': '6'},
                    { 'name': '视频教程', 'value': '7'},
                    { 'name': '唤醒徒弟','value': '8'},
                    { 'name': '面对面收徒', 'value': '9'},
                ],

                parsing_status:[
                    { 'name': '已解析', 'value': '1'},
                    { 'name': '未解析', 'value': '0'},
                ],
                radio:1,
                dialogForm: false,
                formLabelWidth: '200px',
                radioShow:false,
                disabled:true,
                isShow:true,
                form:{
                    id:'',
                    url: '',
                    note:'',
                    purpose:'',
                    status:'',
                    wx:'',
                    qq:'',
                    parsing_status:'',
                    group_num:'',
                }
            }
        },
        methods: {

            formatUrlsStr: function() {
                this.form.url = this.form.url.replace(/(.+)(?:\n)|(?:\r\n)/g, '$1,');
            },

            showDomainDialog(item) {
                if(item){
                    this.isShow = false;
                    this.form.urls = '';
                    this.form.purpose = '';
                    this.form.wx = '';
                    this.form.qq = '';
                    this.form.status = '';
                    this.form.group_num = '';
                    this.form.parsing_status = '';
                }

                this.dialogForm = true;
            },
            submit(){
                quRequest.send(quRequest.Apis.qutuiba.domain.batchModify,{
                    urls: this.form.url,
                    data: JSON.stringify(this.form)
                    }
                ).then(res => {
                    this.$message.success("提交成功！");
                    this.$emit('update')
                }).catch(err => {
                    this.$message.error(`获取内容列表数据失败：${ err.message || '未知错误' }`);
                }).fin(() => {
                    this.loading = false;
                }).done();
                this.dialogForm = false;
            }
        }
    }
</script>


